.about-div{
  width: 100%;
  min-height: 25vh;
  padding-bottom: 16px;
  display: flex;
  flex-direction: column;
  background: rgb(0,146,172);
  background: linear-gradient(90deg, rgba(0,146,172,1) 0%, rgba(0,212,255,1) 100%);

}
.about-title{
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  font-size: calc(16px + 1.5vw);
  color: white;
  font-weight: bold;
}

.about-info-div{
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}
.about-info{
  color: white;
  font-weight: bold;
  width: 100%;
  max-width: 1140px;
  justify-content: left;
  font-size: calc(16px + 0.3vw);
}
.info-div{
  width: 100%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
  justify-content: left;
}
.about-button-div{
  display: flex;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  
}
.about-button{
  display: flex;
  font-size: calc(16px + 0.5vw);
  color: rgb(255, 255, 255);
  background-color: transparent;
  width: auto;
  margin-top: 8px;
  letter-spacing: 1px;
  cursor: pointer;
  box-shadow: inset 0 0 0 0 #ffffff;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
  border: 2px solid #ffffff;
  border-radius: 10px;
}

.about-button:hover{
  box-shadow: inset 400px 0 0 0 #ffffff;
  color: rgb(0, 146, 172);
}