.projects-section-div{
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}
.projects-section-title{
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: calc(16px + 1vw);
  margin-left: auto;
  margin-right: auto;
  font-weight: bold;
  }

.projects-div{
  display: flex;
  flex-direction:row;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.project-div{
  display: flex;
  flex-direction: column;
  justify-content: left;
  width:40%
}

.project-title{
  display: flex;
  font-size: calc(16px + 0.6vw);
  font-weight: bold;
  margin-bottom: 0%;
}
.project-info{
  display: flex;
  text-align: left;
  font-size: calc(16px + 0.3vw);


}
.project-button-div{
  display: flex;
  width: 100%;
  margin-left: auto;
  margin-right: auto;

}
.project-button{
  display: flex;
  font-size: calc(16px + 0.3vw);
  color: rgb(0, 146, 172);
  width: auto;
  margin-top: 8px;
  letter-spacing: 1px;
  cursor: pointer;
  box-shadow: inset 0 0 0 0 #c7ebff;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
  margin-right: 8px;
  border-radius: 10px;
  background-color:  #000a0c
}

.project-button:hover{
  box-shadow: inset 400px 0 0 0 #bad5e5;
}
.project-img{
 width: 60%;
  height: auto;
  border-radius: 10px;
  transition: transform 300ms ease-in-out 0s;
}
.project-img:hover{
  transition: transform 300ms ease-in-out 0s;
}
